@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

.main {
    height: auto;
    width: 100%;
    border-radius: 0px 30px 30px 0px;
    background-color: #F8F9FC;

    .head {
        display: flex;
        justify-content: space-between;

        svg {
            height: 13%;
            width: 13%;
            margin-right: 40px;
            margin-left: auto;
            margin-top: 20px;
            cursor: pointer;
        }

        svg:focus {
            height: 12%;
            width: 12%;
        }
    }
}

.categories {
    margin-left: 3%;
    margin-top: 1%;
    display: flex;
    flex-direction: row;
    column-gap: 40px;
    align-items: center;

    .active {        
        label {
            cursor: pointer;
            font-weight: 600;
        }
    }

    div {
        display: flex;
        flex-direction: row;
        column-gap: 5px;
        align-items: center;

        span {
            display: block;
        }

        label {
            cursor: pointer;
            font-size: 2rem;
            font-weight: 400;
        }
    }
}

