.coins-sort {
    display: flex;
    flex-direction: row;
    column-gap: 60px;
    margin: 40px 40px;
    background-color: white;
    padding: 20px;
    border-radius: 0.5em;

    div {
        cursor: pointer;
        display: flex;
        flex-direction: row;
        align-items: center;
        height: auto;

        label {
            cursor: pointer;
            font-weight: 600;
            opacity: 0.5;
        }

        span {
            height: auto;
            cursor: pointer;
            margin-left: 5px;
        }
    }

    div:first-child {
        label {
            margin-left: 10px;
            color: black;
            opacity: 1;
        }
    }
}

