.row-coin {
    width: 90%;
    height: auto;
    margin-left: 40px;
    display: grid;
    grid-template-columns:0.5fr 0.5fr 1fr 0.5fr 6fr;
    // display: flex;
    height: 60px;
    margin-bottom: 40px;
    // align-items: center;

    .coin-number {
        font-weight: 600;
        margin-left: auto;
        margin-right: auto;
        margin-top: auto;
        margin-bottom: auto;
    }

    img {
        height: 60px;
        width: 60px;
        margin-right: 20px;
    }

    .coin-fullname {
        margin-right: auto;
        margin-top: auto;
        margin-bottom: auto;
        font-weight: 800;
        font-size: 20px;
        margin-right: 20px;
    }

    .coin-shortname {
        margin-left: auto;
        margin-right: auto;
        margin-top: auto;
        margin-bottom: auto;
        font-weight: 800;
        opacity: 0.5;
        font-size: 20px;
    }

    .score {
        height: 100%;
        width: 100%;
        margin-left: 20px;
        background: rgb(246,253,255);
        background: -moz-linear-gradient(90deg, rgba(246,253,255,1) 0%, rgba(252,244,213,1) 35%);
        background: -webkit-linear-gradient(90deg, rgba(246,253,255,1) 0%, rgba(252,244,213,1) 35%);
        background: linear-gradient(90deg, rgba(246,253,255,1) 0%, rgba(252,244,213,1) 35%);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#f6fdff",endColorstr="#fcf4d5",GradientType=1);
        border-radius: 10px;
        box-shadow: inset 2px 2px 7px rgba(0,0,0,0.5);
        display: flex;
        flex-direction: row;

        .line {
            height: 20px;
            width: 40%;
            margin-top: auto;
            margin-bottom: auto;
            margin-left: 20px;
            border-radius: 7px;
            background: rgb(222,186,40);
            background: -moz-linear-gradient(90deg, rgba(222,186,40,1) 0%, rgba(255,210,23,1) 35%);
            background: -webkit-linear-gradient(90deg, rgba(222,186,40,1) 0%, rgba(255,210,23,1) 35%);
            background: linear-gradient(90deg, rgba(222,186,40,1) 0%, rgba(255,210,23,1) 35%);
            filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#deba28",endColorstr="#ffd217",GradientType=1);
        }

        .line2 {
            height: 20px;
            width: 35%;
            margin-top: auto;
            margin-bottom: auto;
            margin-left: 20px;
            border-radius: 7px;
            background: rgb(255,164,9);
            background: -moz-linear-gradient(90deg, rgba(255,164,9,1) 0%, rgba(240,5,0,1) 35%);
            background: -webkit-linear-gradient(90deg, rgba(255,164,9,1) 0%, rgba(240,5,0,1) 35%);
            background: linear-gradient(90deg, rgba(255,164,9,1) 0%, rgba(240,5,0,1) 35%);
            filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffa409",endColorstr="#f00500",GradientType=1);
        }

        .line3 {
            height: 20px;
            width: 36%;
            margin-top: auto;
            margin-bottom: auto;
            margin-left: 20px;
            border-radius: 7px;
            background: rgb(184,64,240);
            background: -moz-linear-gradient(90deg, rgba(184,64,240,1) 0%, rgba(109,142,208,1) 35%, rgba(30,223,175,1) 95%);
            background: -webkit-linear-gradient(90deg, rgba(184,64,240,1) 0%, rgba(109,142,208,1) 35%, rgba(30,223,175,1) 95%);
            background: linear-gradient(90deg, rgba(184,64,240,1) 0%, rgba(109,142,208,1) 35%, rgba(30,223,175,1) 95%);
            filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#b840f0",endColorstr="#1edfaf",GradientType=1);
        }

        .line4 {
            height: 20px;
            width: 10%;
            margin-top: auto;
            margin-bottom: auto;
            margin-left: 20px;
            border-radius: 7px;
            background: rgb(199,96,234);
            background: -moz-linear-gradient(90deg, rgba(199,96,234,1) 0%, rgba(137,160,216,1) 35%, rgba(55,245,193,1) 95%);
            background: -webkit-linear-gradient(90deg, rgba(199,96,234,1) 0%, rgba(137,160,216,1) 35%, rgba(55,245,193,1) 95%);
            background: linear-gradient(90deg, rgba(199,96,234,1) 0%, rgba(137,160,216,1) 35%, rgba(55,245,193,1) 95%);
            filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#c760ea",endColorstr="#37f5c1",GradientType=1);
        }

        .score-img {
            height: 30px;
            width: 30px;
            margin-top: auto;
            margin-bottom: auto;
            z-index: 1;
            margin-left: -15px;
        }

        label {
            margin-top: auto;
            margin-bottom: auto;
            color: #4BC716;
            font: normal normal bold 20px/24px Montserrat;
        }

        .finish-section {
            display: grid;
            grid-template-columns: 0.1fr 1fr 1fr;
            margin-left: auto;
            width: 35%;
            text-align: center;

            img {
                height: 40px;
                margin: auto;
            }

            .coefficient {
                height: 30px;
                border: 1px solid #E70DFF;
                background-color: transparent;
                border-radius: 5px;
                margin: auto;
                display: flex;
                align-items: center;

                label {
                    font-size: 15px;
                    margin: auto;
                    padding-left: 15px;
                    padding-right: 15px;
                    color: #E70DFF;
                }
            }

            .bet-btn {
                height: 40px;
                background-color: #E70DFF;
                border-radius: 30px;
                margin: auto;
                display: flex;
                align-items: center;

                label {
                    font-weight: 500;
                    font-size: 15px;
                    padding-left: 25px;
                    padding-right: 25px;
                    color: white;
                }
            }
        }
    }
    
}

.show-modal {
    opacity: 1!important;
    visibility: visible!important;
    transform: scale(1.0)!important;
    transition: visibility 0s linear 0s, opacity 0.25s 0s, transform 0.25s!important;
    z-index: 2;
}

.connect-wallet-modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0;
    visibility: hidden;
    transform: scale(1.1);
    transition: visibility 0s linear 0.25s, opacity 0.25s 0s, transform 0.25s;

    .connect-wallet-content {
        position: absolute;
        top: 50%;
        left: 50%;
        height: 420px;
        transform: translate(-50%, -50%);
        background-color: white;
        width: 420px;
        border-radius: 30px;

        .connect-wallet-header {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            height: 40px;
            align-items: center;
            padding: 1rem 1.5rem;
            
            label {
                opacity: 0.5;
            }

            span {
                img {
                    height: 25px;
                    width: 25px;
                    cursor: pointer;
                }
            }
        }

        .underline {
            height: 1px;
            width: 100%;
            background-color: lightgray;
        }

        .connect-wallet-body {
            display: flex;
            flex-direction: column;

            h2 {
                margin-top: 40px;
                margin-left: auto;
                margin-right: auto;
                margin-bottom: 55px;
            }

            div {
                display: flex;
                flex-direction: row;
                justify-content: center;

                div {
                    display: flex;
                    flex-direction: column;
                    text-align: center;

                    img {
                        cursor: pointer;
                    }

                    label {
                        cursor: pointer;
                        font-size: 13px;
                        opacity: 0.5;
                    }
                }

                div:first-child {
                    label {
                        color: #6C1AEB;
                        opacity: 1;
                    }
                }
            }
        }
    }
}

